<template>
    <div class="post">
        <div class="post__item">
            <p class="post__title">{{post.title}}</p>
            <p class="post__description">{{post.body}}</p>
        </div>
        <div class="post__btns">
            <MyButton class="btn" @click="$emit('modal', post.id)">Модальное подробнее</MyButton> 
            <MyButton class="btn" @click="$router.push(`/posts/post/${post.id}`)">Подробнее</MyButton> 
            <MyButton class="btn" @click="$emit('remove', post)">Удалить</MyButton> 
        </div>
    </div>
</template>

<script>
export default {
    props: {
        post: {
            type: Object,
            requierd: true
        }
    }
}
</script>

<style scoped>
    .post{
        padding: 5px;
        border: 3px solid blue;
        display: flex;
        justify-content: space-between;
    }

    .post:not(:last-of-type){
        margin-bottom: 15px;
    }

    .post__title{
        text-transform: uppercase;
        font-weight: bolder;
    }

    .post__btns{
        display: flex;
        align-items: center;
    }

    .btn:not(:last-of-type){
        margin-right: 10px;
    }
</style>