<template>
    <div class="pages">
        <p class="page__item" v-for="onePage in totalPages" 
                :key="onePage" 
                :class="{
                    select: onePage === selectPage
                }"
                @click="$emit('changePage', onePage)">{{onePage}}</p>
    </div>
</template>

<script>
export default {
    name: 'Pagination',
    props: {
        totalPages: {
            type: Number,
            requierd: true
        },
        selectPage: {
            type: Number
        }
    }
}
</script>

<style scoped>
    .pages{
        display: flex;
    }

    .page__item{
        padding: 2px;
        cursor: pointer;
    }

    .page__item:not(:last-of-type){
        margin-right: 10px;
    }

    .page__item.select{
        font-weight: bolder;
    }
</style>