<template>
    <select v-model="modelValue" class="select" @change="updateValue">
        <option value="" disabled>Выберите тип сортировки</option>
        <option v-for="option in options" :key="option.value" :value="option.value">
            {{option.name}}
        </option>
    </select>
</template>

<script>
export default {
    name: 'MySelect',
    props: {
        modelValue: {
            type: String,
        },
        options: {
            type: Array,
            default: () => []
        }
    },
    methods: {
        updateValue(e){
            this.$emit('update:modelValue', e.target.value);
        }
    }
}
</script>

