<template>
    <div>
        <div class="postCreate">
            <h3>Создать пост</h3>
            <div class="posts__btns">
                <MyButton @click="dialogVisible = true">Создать пост</MyButton>
                <MySelect :model-value="selectedSort" @update:model-value="setSelectedSort" :options="optionsList" />
            </div>
        </div>
        <MyInput :model-value="searchQuery" @update:model-value="setSearchQuery" placeholder="Поиск" />
        <postList v-if="!showPosts" :posts="sortedAndSearchedPosts" @remove="removePost"/>
        <p v-else>Загрузка</p>
        <Pagination :totalPages="totalPage" :selectPage="page" class="pagination" @changePage="pageChange"/>
        
        
        <Modal v-model="dialogVisible">
            <postForm class="postForm" @create="createPost" />
        </Modal>
    </div>
</template>

<script>
import PostList from '@/components/PostList';
import PostForm from '@/components/PostForm';
import axios from 'axios';
import {mapState, mapGetters, mapMutations, mapActions} from 'vuex';

export default {
    components: {
        PostList, PostForm
    },
    data() {
        return {
            dialogVisible: false,
        }
    },
    methods:{
        ...mapMutations({
            setPosts: 'setPosts',
            setShowPosts: 'setShowPosts',
            setSelectedSort: 'setPosts',
            setSearchQuery: 'setSearchQuery',
            setTotalPage: 'setTotalPage',
            setPage: 'setPage',
        }),
        ...mapActions({
            getPosts: 'getPosts'
        }),
        createPost(post){
            this.posts.push(post);
        },
        removePost(post){
            this.posts = this.posts.filter(p => p.id != post.id)
        },
        pageChange(newPage){
            this.page = newPage;
        }
    },
    mounted(){
        this.getPosts();
    },
    computed: {
        ...mapState({
            posts: state => state.post.posts,
            showPosts: state => state.post.showPosts,
            optionsList: state => state.post.optionsList,
            selectedSort: state => state.post.selectedSort,
            searchQuery: state => state.post.searchQuery,
            page: state => state.post.page,
            limit: state => state.post.limit,
            totalPage: state => state.post.totalPage,
        }),
        ...mapGetters({
            sortedPosts: 'sortedPosts',
            sortedAndSearchedPosts: 'sortedAndSearchedPosts'
        })
    },
    watch: {
        page(){
            this.getPosts();
        }
    }
}
</script>

<style>

    .postForm{
        margin-bottom: 0;
    }

    .posts__btns{
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 15px;
    }

    .pagination{
        margin-top: 10px;
        justify-content: center;
    }
</style>