<template>
    <textarea class="textarea" @input="updateInput" v-model="modelValue"></textarea>
</template>

<script>
export default {
    name: 'MyTextarea',
    props: {
        modelValue: [Number, String]
    },
    methods: {
        updateInput(e){
            this.$emit('update:modelValue', e.target.value)
        }
    }
}
</script>

<style scoped>
    .textarea{
        width: 100%;
        max-width: 100%;
        border: 3px solid blue;
        padding: 10px;
        height: 100px;
    }
</style>