<template>
    <div class="posts" v-if="posts.length > 0">
        <h3>Список постов</h3>
        <transition-group name="posts-list">
            <PostItem v-for="post in posts" :post="post" :key="post.id" @remove="$emit('remove', post)" @modal="$emit('modal', post)" />
        </transition-group>
    </div>
    <div class="noPosts" v-else>
        <h2>Нет постов</h2>
    </div>
</template>

<script>
import PostItem from './PostItem.vue';
export default {
    props: {
        posts: {
            type: Array,
            requierd: true
        }
    },
    components: { PostItem }
}
</script>

<style scoped>
    .posts-list-enter-active,
    .posts-list-leave-active{
        transition: all .4s;
    }

    .posts-list-enter-from,
    .posts-list-leave-to{
        opacity: 0;
    }

    .posts-list-enter-from{
        transform: translateX(-130px);
    }

    .posts-list-leave-to{
        transform: translateX(130px);
    }

    .posts-list-move{
        transition: transform .3s;
    }

</style>