<template>
    <div v-if="!loading">
        <h2>Этот пост о штуке с id = {{post.id}}</h2>
        <h3>Название: {{post.title}}</h3>
        <p>Описание: {{post.body}}</p>
    </div>
    <p v-else>Загрузка</p>
</template>

<script>
import axios from 'axios'

export default {
    data(){
        return{
            post: {
                id: this.$route.params.id,
                title: '',
                body: ''
            },
            loading: true,
        }
    },
    methods: {
        async getPost(){
            try {
                let postUrl = await axios.get('https://jsonplaceholder.typicode.com/posts', {
                    params: {
                        id: this.post.id
                    }
                });

                this.post.title = postUrl.data[0].title;
                this.post.body = postUrl.data[0].body;
            } catch(e){
                alert(e);
            } finally{
                this.loading = false;
            }
        }
    },
    mounted(){
        this.getPost();
    }
}
</script>

