<template>
    <div>
        <div class="postCreate">
            <h3>Создать пост</h3>
            <div class="posts__btns">
                <MyButton @click="dialogVisible = true">Создать пост</MyButton>
                <MySelect v-model="selectedSort" :options="optionsList" />
            </div>
        </div>
        <MyInput v-model="searchQuery" placeholder="Поиск" />
        
        <postList v-if="!showPosts" :posts="sortedAndSearchedPosts" @remove="removePost" @modal="getPost" />
        <p v-else>Загрузка</p>

        <Pagination :totalPages="totalPage" :selectPage="page" class="pagination" @changePage="pageChange"/>
        
        <Modal v-model="dialogVisible">
            <postForm class="postForm" @create="createPost" />
        </Modal>

        <Modal v-model="postVis">
            <PostModal :post="post" />
        </Modal>
    </div>
</template>

<script>
import PostList from '@/components/PostList';
import PostForm from '@/components/PostForm';
import PostModal from '@/components/PostModal';
import axios from 'axios';

export default {
    components: {
        PostList, PostForm, PostModal
    },
    data() {
        return {
            posts: [],
            dialogVisible: false,
            showPosts: true,
            optionsList: [
                {value: 'body', name: 'По описанию'},
                {value: 'title', name: 'По названию'},
            ],
            selectedSort: '',
            searchQuery: '',
            page: 1,
            limit: 10,
            totalPage: 0,
            post: {},
            postVis: false,
        }
    },
    methods:{
        createPost(post){
            this.posts.push(post);
        },
        removePost(post){
            this.posts = this.posts.filter(p => p.id != post.id)
        },
        async getPosts(){
            try{
                let postsUrl = await axios.get('https://jsonplaceholder.typicode.com/posts', {
                    params: {
                        _page: this.page,
                        _limit: this.limit
                    }
                });
                this.totalPage = Math.ceil(postsUrl.headers['x-total-count'] / this.limit);
                this.posts = postsUrl.data;
            } catch (e){
                alert(e);
            } finally{
                this.showPosts = false;
            }
        },
        async getPost(postId){
            try{
                let postUrl = await axios.get('https://jsonplaceholder.typicode.com/posts', {
                    prarms: {
                        id: postId.id
                    }
                });

                this.post = postUrl.data[0];
            } catch(e){
                alert(e);
            } finally{
                this.postVis = true;
            }
        },
        pageChange(newPage){
            this.page = newPage;
        }
    },
    mounted(){
        this.getPosts();
    },
    computed: {
        sortedPosts(){
            return [...this.posts].sort((post1, post2) => {
                return post1[this.selectedSort]?.localeCompare(post2[this.selectedSort]);
            })
        },
        sortedAndSearchedPosts(){
            return this.sortedPosts.filter((post) => post.title.toLowerCase().includes(this.searchQuery.toLowerCase()))
        }
    },
    watch: {
        page(){
            this.getPosts();
        }
    }
}
</script>

<style>

    .postForm{
        margin-bottom: 0;
    }

    .posts__btns{
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 15px;
    }

    .pagination{
        margin-top: 10px;
        justify-content: center;
    }
</style>