import axios from 'axios';
export let postModule = {
    state: () => ({
        posts: [],
        showPosts: true,
        optionsList: [
            {value: 'body', name: 'По описанию'},
            {value: 'title', name: 'По названию'},
        ],
        selectedSort: '',
        searchQuery: '',
        page: 1,
        limit: 10,
        totalPage: 0,
    }),
    getters: {
        sortedPosts(state){
            return [...state.posts].sort((post1, post2) => {
                return post1[state.selectedSort]?.localeCompare(post2[state.selectedSort]);
            })
        },
        sortedAndSearchedPosts(state, getters){
            return getters.sortedPosts.filter((post) => post.title.toLowerCase().includes(state.searchQuery.toLowerCase()))
        },
    },
    mutations: {
        setPosts(state, posts){
            state.posts = posts;
        },
        setShowPosts(state, showPosts){
            state.showPosts = showPosts;
        },
        setSelectedSort(state, selectedSort){
            state.selectedSort = selectedSort;
        },
        setSearchQuery(state, searchQuery){
            state.searchQuery = searchQuery;
        },
        setTotalPage(state, totalPage){
            state.totalPage = totalPage;
        },
        setPage(state, page){
            state.page = page;
        }
    },
    actions: {
        async getPosts({state, commit}){
            try{
                let postsUrl = await axios.get('https://jsonplaceholder.typicode.com/posts', {
                    params: {
                        _page: state.page,
                        _limit: state.limit
                    }
                });
                commit('setTotalPage', Math.ceil(postsUrl.headers['x-total-count'] / state.limit));
                commit('setPosts', postsUrl.data)
            } catch (e){
                alert(e);
            } finally{
                commit('setShowPosts', false)
            }
        },
    },
    namespace: 'post',
}