<template>
    <form action="" class="form" @submit.prevent>
        <h2>Создать пост</h2>
        <MyInput v-model="post.title" placeholder="Введите заголовок" />
        <MyInput v-model="post.body" placeholder="Введите описание" />
        <MyButton @click="createPost">Создать пост</MyButton>
    </form>
</template>

<script>
export default {
    data() {
        return {
            post: {
                title: "",
                body: ""
            }
        };
    },
    methods: {
        createPost() {
            this.post.id = Date.now();
            this.$emit("create", this.post);
            this.post = {
                title: "",
                description: ""
            };
        }
    },
}
</script>

<style scoped>

    .form{
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        margin-bottom: 15px;
        padding: 5px;
    }

    .form h2{
        align-self: flex-start;
        margin-bottom: 10px;
    }
</style>