<template>
    <div class="modal" v-if="modelValue" @click.stop="closeModal">
        <div class="modal__inner" @click.stop>
            <slot></slot>
        </div>
    </div>
</template>

<script>
import toggleMixin from '@/mixins/toggleMixin';

export default {
    name: 'Modal',
    mixins: [toggleMixin]
}
</script>

<style>
    .modal{
        position: absolute;
        background: rgba(0,0,0,0.7);
        width: 100%;
        height: 100%;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .modal__inner{
        background: white;
        padding: 15px;
        min-width: 300px;
        min-height: 50px;
        border-radius: 10px;
    }
</style>