<template>
    <form action="" class="form" @submit.prevent>
        <h2 class="form__title">Введите комментарий</h2>
        <MyInput class="form__input" placeholder="Введите имя"  v-model="comment.name" />
        <MyTextarea class="form__textare" placeholder="Введите ваш комментарий" v-model="comment.body" ></MyTextarea>
        <MyButton class="form__button" @click="createComment">Отправить</MyButton>
    </form>
</template>

<script>
export default {
    data(){
        return {
            comment: {
                name: '',
                body: ''
            },
        }
    },
    methods: {
        createComment(){
            this.comment.id = Date.now();
            this.$emit('createComment', this.comment);
            this.comment= {
                name: '',
                body: ''
            }
        }
    }
}
</script>

<style>
    .form{
        width: 100%;
        display: flex;
        flex-direction: column;
        margin-top: 15px;
    }

    .form__title{
        margin-bottom: 20px;
    }

    .form__input{
        width: auto !important;
        align-self: flex-start;
    }

    .form__button{
        margin-top: 5px;
        width: auto;
        align-self: flex-end;
    }
</style>