<template>
    <input class="input" type="text" :value="modelValue" @input="updateInput">
</template>

<script>
export default {
    name: 'MyInput',
    props: {
        modelValue: [Number, String]
    },
    methods: {
        updateInput(e){
            this.$emit('update:modelValue', e.target.value)
        }
    }
}
</script>

<style scoped>
    .input{
        width: 100%;
        border: 3px solid blue;
        padding: 3px;
        height: 30px;
        margin-bottom: 5px;
    }
</style>