<template>
    <button class="button">
        <slot></slot>
    </button>
</template>

<script>
export default {
    name: 'MyButton'
}
</script>

<style>
    .button{
        border: 3px solid blue;
        transition: .3s all;
        cursor: pointer;
        color: blue;
        padding: 5px;
    }

    .button:hover{
        color: white;
        background: blue;
    }
</style>